.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-slide-in 1s ease-in-out;
          animation: App-logo-slide-in 1s ease-in-out;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-slide-in {
  from { -webkit-transform: translateY(-100px); transform: translateY(-100px); }
  to { -webkit-transform: translateY(0px); transform: translateY(0px); }
}

@keyframes App-logo-slide-in {
  from { -webkit-transform: translateY(-100px); transform: translateY(-100px); }
  to { -webkit-transform: translateY(0px); transform: translateY(0px); }
}


